<template>
  <div class="alert m-5 mobile-font">
    <h3>Enter private key</h3>
    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Private key</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          :maxlength="64"
          v-model="privatekey"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="Private key..."
          aria-label="Private-key">
      </div>
    </div>
    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Address</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          v-model="address"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="address"
          aria-label="address"
          readonly="readonly">
      </div>

    </div>
    <h3>Cosignatories:</h3>

    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Address</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          v-model="addressCo1"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="address"
          aria-label="address"
          readonly="readonly">
      </div>
    </div>

    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Private key</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          :maxlength="64"
          v-model="privatekeyCo1"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder=""
          aria-label="Private-key"
          readonly="readonly">
      </div>
    </div>
    <hr>
    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Address</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          v-model="addressCo2"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="address"
          aria-label="address"
          readonly="readonly">
      </div>
    </div>
    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Private key</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          :maxlength="64"
          v-model="privatekeyCo2"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder=""
          aria-label="Private-key"
          readonly="readonly">
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';

import AccessController from '@/modules/controllers/access';

const user = AccessController.create();
export default {
  name: 'Cosignatories',
  data() {
    return {
      address: '',
      publickey: '',
      privatekey: '',
      privatekeyCo1: '',
      privatekeyCo2: '',
      addressCo1: '',
      addressCo2: '',
    };
  },
  methods: {
    ...mapGetters(['getNetwork']),
    ...mapActions(['setNetwork']),
    async createAccount() {
      try {
        user.network = this.network;
        await user.login(this.privatekey);
        this.address = user.data.master.address;
        this.publickey = user.data.master.publicKey;
        this.privatekey = user.data.master.privateKey;
        this.privatekeyCo1 = user.data.cosigners.Cosig1.privateKey;
        this.privatekeyCo2 = user.data.cosigners.Cosig2.privateKey;
        this.addressCo1 = user.data.cosigners.Cosig1.address;
        this.addressCo2 = user.data.cosigners.Cosig2.address;
      } catch (e) {
        this.$bus.$emit('alert', e);
      }
    },
  },
  computed: {
    network: {
      set(val) {
        this.setNetwork(val);
      },
      get() {
        return this.getNetwork();
      },
    },
  },
  watch: {
    privatekey() {
      this.createAccount();
    },
    network() {
      this.createAccount();
    },
  },
};
</script>
