/* eslint-disable import/no-unresolved */
/* eslint-disable import/extensions */
/* eslint-disable consistent-return */
/* eslint-disable no-throw-literal */
import EventEmitter from 'events';
import nem from 'nem-sdk';
import deffered from '@/modules/utils/deffer';
/**
 * @param {Array} arr
 * @returns {Array} shuffled array
 */

function getRandArr(arr) {
  return arr.sort(() => 0.5 - Math.random());
}
class API extends EventEmitter {
  /**
   * @param {integer} [deadline=3] add time to "deadline" for supporting actual timestamp
   * in next attempt to send transaction
  */

  constructor(deadline = 1) {
    super();
    this.$plusTimeDeadline = deadline;
    this.$network = {};
    this.netTypes = ['mainnet', 'testnet'];
  }

  setNodes() {
    switch (this.$network.type) {
      case 'mainnet':
        this.$nodes = nem.model.nodes.mainnet;
        break;
      case 'mainnethttps':
        this.$nodes = nem.model.nodes.mainnethttps;
        break;
      default:
        this.$nodes = nem.model.nodes.testnet;
    }
  }
  /**
   *
   * @param {String} address - nem address
   * @throws {Error} if address isn't valid it'll return Error
   * @returns {boolean} otherwise returns true
 */

  validationAddress(address) {
    if (!nem.model.address.isValid(address)) throw new Error('the address isn\'t valid');
    if (!nem.model.address.isFromNetwork(address, this.$network.id)) throw new Error('this address belongs to another network');
    if (!this.$nodes.length) throw new Error('not active nodes! check your network connection');
    return true;
  }
  /**
   * @param {String} publickey
   * @returns {String} nem address
 */

  pubkeyToAddress(publickey) {
    return nem.model.address.toAddress(publickey, this.$network.id);
  }
  /**
     * @throws {throw} if list of nodes is empty
     * @returns {Object} Prepared Endpoint
  */

  prepareEndpoint() {
    if (!this.$nodes.length) throw { code: 3, message: 'not active nodes! check your network connection' };
    return nem.model.objects.create('endpoint')(
      this.$nodes[Math.floor(Math.random() * this.$nodes.length)].uri,
      this.$port,
    );
  }
  /**
   * @param {Object} endpoint
   * @returns {integer} current chain's time
   */

  async fixTimeStamp(endpoint) {
    this.endpoint = endpoint;
    const timeStamp = await nem.com.requests.chain.time(this.endpoint);
    return Math.floor(timeStamp.receiveTimeStamp / 1000);
  }

  /**
   * @description
   * @param {Object} common
   * @param {Object} transactionEntity - prepared transaction
   * @param {Integer} [due = 50] - transaction's lifetime
   * @method Deferred#resolve
   * @param {Object} result - NemAnnounceResult see: https://nemproject.github.io/#nemAnnounceResult
   * @method Deferred#reject
   * @param {Error} error - Not active nodes
   * @returns {Deferred}
  */

  async send(common, transactionEntity, due = 50) {
    const $transactionEntity = transactionEntity;
    const deffer = deffered();
    let endpoint;
    const loop = async () => {
      try {
        endpoint = this.prepareEndpoint();
        if ($transactionEntity.otherTrans) {
          $transactionEntity.timeStamp = await this.fixTimeStamp(endpoint);
          $transactionEntity.deadline = $transactionEntity.timeStamp + due;
        }
        const result = await nem.model.transactions.send(common, $transactionEntity, endpoint);
        if (result.code >= 2) throw new Error(result.message);
        return deffer.resolve(result);
      } catch (err) {
        if (err.code === 3) return deffer.reject(err);
        this.remove(endpoint.host);
        $transactionEntity.deadline += this.$plusTimeDeadline;
        loop();
      }
    };
    loop();
    return deffer.promise;
  }

  /**
   * @description Getting confirmed incoming transactions
   * @param {string} address
   * @param {string} [txHash=''] - transaction's hash
   * @returns {Deferred}
   * @method Deferred#resolve
   * @param {Object} data - Data of transaction
   * @method Deferred#reject
   * @param {Error} error - Not active nodes
  */
  transactionIncoming(address, txHash = '') {
    this.validationAddress(address);
    const deffer = deffered();
    let endpoint;
    const loop = async () => {
      try {
        endpoint = this.prepareEndpoint();
        const data = await nem.com.requests
          .account.transactions.incoming(endpoint, address, txHash);
        return deffer.resolve(data);
      } catch (err) {
        if (err.code === 3) return deffer.reject(err);
        this.remove(endpoint.host);
        loop();
      }
    };
    loop();
    return deffer.promise;
  }

  /**
   * @description Getting data of account
   * @param {string} address
   * @returns {Deferred}
   * @method Deferred#resolve
   * @param {Object} data - Data of address {publick key, balance, cosigners...}
   * @method Deferred#reject
   * @param {Error} error - not active nodes
   * @see NEM-SDK
  */
  getAddressData(address) {
    this.validationAddress(address);
    const deffer = deffered();
    let endpoint;
    const loop = async () => {
      try {
        endpoint = this.prepareEndpoint();
        const data = await nem.com.requests.account.data(endpoint, address);
        return deffer.resolve(data);
      } catch (err) {
        if (err.code === 3) return deffer.reject(err);
        this.remove(endpoint.host);
        loop();
      }
    };

    loop();
    return deffer.promise;
  }

  /**
   * @description removing from list not working node
   * @param {string} element
   * @returns {Object} filtered array
  */
  remove(element) {
    this.$nodes = [...this.$nodes.filter(e => e.uri !== element)];
  }
  /**
   * @description Creating an endpoint object
   * @param {string} address
   * @param {integer} [count=1] count of connections
   * @returns {Deferred}
   * @method Deferred#resolve
   * @param {Object} connections - list of created connections
   * @method Deferred#reject
   * @param {Object} error - not active nodes
  */

  async createConnector(address, count = 1) {
    this.validationAddress(address);
    const deffer = deffered();
    const connections = [];
    let nodes = [...getRandArr(this.$nodes)];

    const connect = async () => {
      if (
        connections.length === count
        || (!nodes.length && connections.length)
      ) return deffer.resolve(connections);

      if ((!nodes.length)
          && (!connections.length)) return deffer.reject({ message: 'not active nodes! check your network connection' });
      try {
        const endpoint = nem.model.objects.create('endpoint')(nodes[0].uri, this.$socketPort);
        const websocketConnect = nem.com.websockets.connector.create(endpoint, address);
        await websocketConnect.connect();
        connections.push(websocketConnect);
      } catch (e) {
        console.error('connectorCreateMulti: ', e);
      }
      nodes = nodes.splice(1);
      connect();
    };
    connect();
    return deffer.promise;
  }
  /**
   *
   * @param {Object} connector
   */

  subscribeAccountData(connector) {
    nem.com.websockets.requests.account.data(connector);
    nem.com.websockets.subscribe.account.data(connector, (r) => {
      this.emit(`account-data-${connector.listener}`, r);
    });
  }
  /**
   *
   * @param {Object} connector
   */

  subscribeTransactionConfirmed(connector) {
    const socket = nem.com.websockets;
    nem.com.websockets.requests.account.data(connector);
    socket.subscribe.account.transactions
      .confirmed(connector, (r) => {
        this.emit(`transaction-confirmed-${connector.listener}`, r);
      });
  }
  /**
   *
   * @param {Object} connector
   */

  subscribeTransactionUnconfirmed(connector) {
    nem.com.websockets.requests.account.data(connector);
    nem.com.websockets.subscribe.account.transactions
      .unconfirmed(connector, (r) => {
        this.emit(`transaction-unconfirmed-${connector.listener}`, r);
      });
  }
  /**
   * @param {string} net type of network
  */

  set network(net) {
    this.$network.type = net;
    this.$port = this.netTypes.includes(this.$network.type)
      ? nem.model.nodes.defaultPort
      : nem.model.nodes.defaultHttpsPort;
    this.$socketPort = this.netTypes.includes(this.$network.type)
      ? nem.model.nodes.websocketPort
      : nem.model.nodes.websocketHttpsPort;
    this.$network.id = net === 'testnet' ? -104 : 104;
    this.setNodes();
  }
}
export { API as default };
