<template>
  <div class="row">
    <svg
      @click="mobileToggleBar"
      class="btn-menu mobile d-lg-none"
      viewBox="0 0 32 32"
      width="32"
      height="32"
      fill="none"
      stroke="currentcolor"
      stroke-linecap="round"
      stroke-linejoin="round"
      stroke-width="2">
      <path d="M4 8 L28 8 M4 16 L28 16 M4 24 L28 24"/>
    </svg>
    <div
      class="col-lg-3 col-sm-12 d-lg-block m-0 p-0"
      :class="[isVisibleLeftBar]">
      <nav class="navbar w-100">
        <svg
          @click="switchLeftBar"
          class="btn-menu"
          viewBox="0 0 32 32"
          width="32"
          height="32"
          fill="none"
          stroke="currentcolor"
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2">
          <path
            d="M13 2 L13 6 11 7 8 4 4 8 7 11 6 13 2 13 2 19 6 19 7 21 4 24 8 28 11 25 13 26
               13 30 19 30 19 26 21 25 24 28 28 24 25 21 26 19 30 19 30 13 26 13 25 11 28 8
               24 4 21 7 19 6 19 2 Z"/>
          <circle
            cx="16"
            cy="16"
            r="4"/>
        </svg>
      </nav>
      <div class="br">
        <component :is="leftBar"/>
      </div>
    </div>
    <div
      class="col-lg-9 col-sm-12 pr-0 pl-0 d-lg-block"
      :class="[isVisibleRightBar]">
      <nav class="navbar w-100 mobile-font">
        <ul
          v-if="isLoggedIn"
          class="navbar-nav flex-row ml-lg-auto w-lg-100 w-sm-75">
          <li class="nav-item mr-1">
            <span class="d-none d-lg-block float-left mr-1">
              Address:</span><span> {{ address }}
              </span>
          </li>
          <li class="nav-item mr-1">
            <span class="d-none d-lg-block float-left mr-1">
              Balance:</span><span>{{ balance }} XEM
              </span>
          </li>
          <li class="nav-item mr-1">
            <span class="d-none d-lg-block float-left mr-1">
              Network:</span><span>{{ network }}
              </span>
          </li>
        </ul>
      </nav>
      <component :is="rightBar"/>
    </div>
  </div>
</template>

<script>
// checked
import { mapActions, mapGetters } from 'vuex';
import DialogList from '@/components/DialogList';
import Menu from '@/components/Menu';
import ContainerChat from '@/components/ContainerChat';
import Login from '@/components/Login';
import Signup from '@/components/Signup';
import Nodes from '@/components/Nodes';
import Cosignatories from '@/components/Cosignatories';


export default {
  name: 'Container',
  components: {
    DialogList,
    ContainerChat,
    Menu,
    Login,
    Signup,
    Nodes,
    Cosignatories,
  },
  data() {
    return {
      isVisibleLeftBar: 'd-none',
      isVisibleRightBar: '',
    };
  },
  methods: {
    ...mapActions([
      'setMsg',
      'setMe',
      'setLeftBar']),
    switchLeftBar() {
      const bar = this.leftBar === 'DialogList' ? 'Menu' : 'DialogList';
      this.setLeftBar(bar);
    },
    mobileToggleBar() {
      this.isVisibleLeftBar = !this.isVisibleLeftBar ? 'd-none' : null;
      this.isVisibleRightBar = !this.isVisibleRightBar ? 'd-none' : null;
    },
  },
  computed: {
    ...mapGetters({
      rightBar: 'getRightBar',
      leftBar: 'getLeftBar',
      network: 'getNetwork',
      balance: 'getBalance',
      address: 'getMe',
      isLoggedIn: 'getLoggedIn',
    }),
  },
  mounted() {
    this.$bus.$on('mobileToggleBar', () => {
      this.mobileToggleBar();
    });
  },
};
</script>

