import Access from '@/modules/controllers/access';
import Account from '@/modules/controllers/account';
import CreateFilter from '@/modules/utils/filter.transaction';
import { PreparePrivateMessage } from '@/modules/utils/message';
import store from '@/modules/store';

export default async function () {
  const access = Access.create();
  const account = new Account();
  account.network = access.network;
  const { address, privateKey } = access.data.master;
  const balance = await account.getBalance(address);
  store.dispatch('setBalance', balance);
  account.api.on(`account-balance-${address}-self`, (_balance) => {
    store.dispatch('setBalance', _balance);
  });

  await account.BalanceSocket(address, 'self');
  await account.TransactionUnconfirmed(address, 'self');
  /*
        * filtering transactions
        * argument 1 : 4100 (type of multisig transaction)
        * argument 2 : 257 (type of transfer transaction)
        * argument 3 : 2 (type of encrypted payload)
        * argument 4 : address (recipient)
      */
  const filterChat = new CreateFilter(4100, 257, 2, address);

  // listening to incoming transaction
  account.api.on(`transaction-unconfirmed-${address}-self`, async (trans) => {
    const sender = account.api.pubkeyToAddress(trans.transaction.otherTrans.signer);

    if (store.getters['chats/getIsBannedAddr'](sender)) return false;
    if (filterChat.check(trans)) {
      const text = await PreparePrivateMessage(trans, privateKey, true);
      store.dispatch('chats/setMessage', text);
    }
  });
}
