<template>
  <nav>
    <ul class="list-group list-group-flush">
      <li
        v-if="!getLoggedIn"
        class="list-group-item">
        <button
          @click = "menue('Signup')"
          type="button"
          class="btn btn-primary btn-custom">Sign-up</button>
      </li>
      <li class="list-group-item">
        <button
          v-if="!getLoggedIn"
          @click = "menue('Login')"
          type="button"
          class="btn btn-primary btn-custom">Login</button>
        <button
          v-if="getLoggedIn"
          @click="logout"
          type="button"
          class="btn btn-primary btn-custom">Logout</button>
      </li>
      <li class="list-group-item">
        <button
          @click = "menue('Nodes')"
          type="button"
          class="btn btn-primary btn-custom">Nodes</button>
      </li>
      <li class="list-group-item">
        <button
          @click = "menue('Cosignatories')"
          type="button"
          class="btn btn-primary btn-custom">Cosignatories</button>
      </li>
      <li class="list-group-item">
        <label>Select Network</label>
        <select
          class="form-control"
          v-model="network">
          <option selected>mainnethttps</option>
          <option>mainnet</option>
          <option>testnet</option>
        </select>
      </li>
      <li class="list-group-item">
        <SoundButton/>
      </li>
    </ul>
  </nav>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
import SoundButton from '@/components/Sound';

export default {
  name: 'Menu',
  components: {
    SoundButton,
  },

  data() {
    return {};
  },

  methods: {
    ...mapActions([
      'setRightBar',
      'setNetwork',
    ]),
    ...mapGetters(['getNetwork']),
    menue(item) {
      this.$bus.$emit('mobileToggleBar');
      this.setRightBar(item);
    },
    logout() {
      document.location.href = '';
    },
  },

  computed: {
    ...mapGetters(['getLoggedIn']),
    network: {
      set(val) {
        this.setNetwork(val);
      },
      get() {
        return this.getNetwork();
      },
    },
  },
};
</script>

<style scoped>
ul {
    display: block;
    margin: 0 auto;
}
li {
    text-align: center;
}
.btn-custom {
  width: 100%;
  background-color: #6AACDE!important;
  border-color: #6AACDE;
}
.btn-custom:hover {
  border-color: #afdafb;
}
label {
  color: #6aacde;
}
</style>
