<template>
  <div class="left-bar">
    <div
      v-if="getLoggedIn"
      class="collapse-menu" >
      <span
        data-target="chatCollapsed"
        @click="collapse">Chats</span>
      <svg
        data-target="chatAddCollapsed"
        @click="collapse"
        id="i-plus"
        viewBox="0 0 32 32"
        fill="none"
        stroke="currentcolor"
        stroke-linecap="round"
        stroke-linejoin="round"
        stroke-width="2">
        <path
          data-target="chatAddCollapsed"
          d="M16 2 L16 30 M2 16 L30 16"/>
      </svg>
      <DialogAdd
        :type="'chat'"
        :placeholder="'Address...'"
        v-show="chatAddCollapsed" />
    </div>
    <b-collapse
      id="collapsedchat"
      v-model="chatCollapsed">
      <ul class="nav bd-sidenav conversations-list">
        <li v-if="chatLoader">
          <Loader
            :height="50"/>
        </li>
        <li
          class="one"
          v-if="chatsCounter === 0">No Chats yet...</li>
        <dialog-item
          v-for="chat in chats"
          :address="chat.address"
          :encrypt= true
          :maxchar="975"
          :type="'chats'"
          :key="chat.address"
        />
      </ul>
    </b-collapse>
    <div class="collapse-menu" >
      <span
        data-target="liveCollapsed"
        @click="collapse">Lives</span>
      <svg
        data-target="liveAddCollapsed"
        @click="collapse"
        id="i-plus"
        viewBox="0 0 32 32"
        fill="none"
        stroke="currentcolor"
        stroke-linecap="round"
        stroke-linejoin="round"
        stroke-width="2">
        <path
          data-target="liveAddCollapsed"
          d="M16 2 L16 30 M2 16 L30 16"/>
      </svg>
      <DialogAdd
        :type="'live'"
        :placeholder="'Address...'"
        v-if="liveAddCollapsed" />
    </div>
    <b-collapse
      id="collapsedlive"
      v-model="liveCollapsed">
      <ul class="nav bd-sidenav conversations-list">
        <li v-if="liveLoader">
          <Loader
            :height="50"/>
        </li>
        <li
          class="one"
          v-if="livesCounter === 0">No Lives yet...</li>
        <dialog-item
          v-for="live in lives"
          :address="live.address"
          :encrypt= false
          :maxchar="1024"
          :type="'lives'"
          :key="live.address"
        />
      </ul>
    </b-collapse>
    <div
      v-if="getLoggedIn"
      class="collapse-menu" >
      <span
        data-target="groupCollapsed"
        @click="collapse">Groups</span>
      <svg
        data-target="groupAddCollapsed"
        @click="collapse"
        id="i-plus"
        viewBox="0 0 32 32"
        fill="none"
        stroke="currentcolor"
        stroke-linecap="round"
        stroke-linejoin="round"
        stroke-width="2">
        <path
          data-target="groupAddCollapsed"
          d="M16 2 L16 30 M2 16 L30 16"/>
      </svg>
      <DialogAdd
        :type="'group'"
        :placeholder="'Private key...'"
        v-show="groupAddCollapsed" />
    </div>
    <b-collapse
      id="collapsedgroup"
      v-model="groupCollapsed">
      <ul class="nav bd-sidenav conversations-list">
        <li v-if="groupLoader">
          <Loader
            :height="50"/>
        </li>
        <li
          class="one"
          v-if="groupsCounter === 0">No Groups yet...</li>
        <dialog-item
          v-for="group in groups"
          :address="group.address"
          :encrypt= true
          :maxchar="975"
          :type="'groups'"
          :key="group.address"
        />
      </ul>
    </b-collapse>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import DialogItem from '@/components/DialogItem';
import DialogAdd from '@/components/DialogAdd';
import Loader from '@/components/Loader';

export default {
  name: 'DialogList',
  components: {
    DialogItem,
    DialogAdd,
    Loader,
  },

  data() {
    return {
      chatCollapsed: false,
      liveCollapsed: true,
      groupCollapsed: false,
      chatAddCollapsed: false,
      liveAddCollapsed: false,
      groupAddCollapsed: false,
      chatLoader: false,
      liveLoader: false,
      groupLoader: false,
    };
  },

  methods: {
    collapse(item) {
      this[item.target.dataset.target] = this[item.target.dataset.target] !== true;
    },
  },

  computed: {
    ...mapGetters({
      chats: 'chats/getChats',
      lives: 'lives/getLives',
      groups: 'groups/getGroups',
      getLoggedIn: 'getLoggedIn',
    }),

    chatsCounter() {
      return Object.keys(this.chats).length;
    },
    livesCounter() {
      return Object.keys(this.lives).length;
    },
    groupsCounter() {
      return Object.keys(this.groups).length;
    },
    isLoginned() {
      return Object.keys(this.chats).length;
    },
  },

  mounted() {
    this.$bus.$on('conversation-collapse', (type) => {
      this[`${type}Collapsed`] = true;
      this[`${type}AddCollapsed`] = false;
    });
    this.$bus.$on('conversation-loader', (type, stat) => {
      this[`${type}Loader`] = stat;
    });
  },

};
</script>

