<template>
  <div class="alert m-5">
    <h4 class="alert-heading">Log-in</h4>
    <p>Enter private key</p>
    <hr>
    <div class="input-group mb-3">
      <input
        type="text"
        :maxlength="64"
        v-model="privatekey"
        class="form-control"
        placeholder="Private key..."
        aria-label="Private-key">
      <div class="input-group-append">
        <button
          class="btn btn-primary"
          @click="login"
          type="button"
          :disabled="isDisabled"
        >LOGIN</button>
      </div>
    </div>
    <Loader
      :height="220"
      v-show="isDisabled"/>
  </div>

</template>
<script>
/* eslint-disable modules */

import { mapActions, mapGetters } from 'vuex';
import AccessController from '@/modules/controllers/access';
import Loader from '@/components/Loader';

const user = AccessController.create();
export default {
  name: 'Login',
  components: {
    Loader,
  },

  data() {
    return {
      privatekey: '',
      isDisabled: false,
    };
  },

  methods: {
    ...mapActions([
      'setMe',
      'setLoggedIn',
      'setRightBar',
      'setLeftBar',
      'setNetwork',
    ]),
    ...mapGetters(['getNetwork']),
    async login() {
      this.isDisabled = true;

      try {
        user.network = await this.getNetwork();
        await user.login(this.privatekey);
        if (!await user.validateAccount()) throw new Error('Account isn\'t valid');
        this.setMe(user.data.master.address);
        this.setLoggedIn(true);
        this.setRightBar('ContainerChat');
        this.setLeftBar('DialogList');
      } catch (e) {
        this.isDisabled = false;
        this.$bus.$emit('alert', e);
        return;
      }
      this.isDisabled = false;
    },
  },

  computed: {
    network: {
      set(val) {
        this.setNetwork(val);
      },
      get() {
        return this.getNetwork();
      },
    },
  },
};
</script>
<style scoped>
.alert {
    background: #6aacde;
    color: #fff;
}
</style>
