

<template>
  <div class="container-fluid">
    <Container/>
    <Alert
      :alerts=errors
    />
  </div>
</template>

<script>
/* eslint-disable import/extensions */
/* eslint-disable import/no-unresolved */
import { mapGetters } from 'vuex';
import Container from '@/components/Container';
import Alert from '@/components/Alert';
import Account from '@/modules/models/account';
import { AddDialog, DelDialog } from '@/modules/models/dialog';

export default {
  name: 'App',
  components: {
    Container,
    Alert,
  },

  data() {
    return {
      errors: [],
    };
  },

  computed: {
    ...mapGetters({ Me: 'getMe' }),
  },

  methods: {
    load(type, stat) {
      this.$bus.$emit('conversation-loader', type, stat);
    },
    addDefaultLive() {
      this.$bus.$emit('AddDialog', {
        type: 'live',
        address: 'NBKKSDBLMFQV5P4MYGF72ZQD6XBRZSHDHXK2NEWS',
      });
      this.$bus.$emit('AddDialog', {
        type: 'live',
        address: 'NBVVN3CGEDS6WK35DP2NONTADLPJCEH3MYNUTECH',
      });
      this.$bus.$emit('AddDialog', {
        type: 'live',
        address: 'NCMVUTCBPRCDCTORKJTYKSKX7MKFLET7L5B7HELP',
      });
      this.$bus.$emit('conversation-collapse', this.type);
    },
  },

  mounted() {
    window.addEventListener('load', () => {
      this.addDefaultLive();
    });
    this.$bus.$on('alert', (alert) => {
      this.errors.push(alert);
      throw new Error(alert);
    });

    this.$bus.$on('AddDialog', async (data) => {
      try {
        this.load(data.type, true);
        await AddDialog(data);
      } catch (err) {
        this.$bus.$emit('alert', err);
      } finally {
        this.load(data.type, false);
      }
    });
    this.$bus.$on('DelDialog', async (data) => {
      try {
        DelDialog(data);
      } catch (err) {
        this.$bus.$emit('alert', err);
      }
    });
  },

  watch: {
    async Me() {
      try {
        await Account(this.Me);
      } catch (err) {
        this.$bus.$emit('alert', err);
      }
    },

    errors() {
      setTimeout(() => {
        if (this.errors.length) this.errors = this.errors.splice(1);
      }, 8000);
    },
  },
};
</script>
<style>
html {
  /* position:fixed; */
  width: 100%;
}
</style>
