<template>
  <div class="row-fluid">
    <div
      class="w-100"
      v-if="isActiveChat">
      <div
        class="chat-container col-12"
        ref="chatContainer">
        <h4
          class="text-center"
          v-if="!emptymsg">No messages here yet...</h4>
        <message-body
          v-for="message in messages"
          :key="message.timestamp"
          :text="message.text"
          :timestamp="message.timestamp"
          :out="message.out"
          :sender="message.sender"
        />
      </div>
      <div class="col-12">
        <message-input v-if = "isLoggedIn"/>
      </div>
    </div>
    <div
      class="col"
      v-else>
      <h4 class="text-center">Please select a chat to start messaging...</h4>
    </div>
  </div>
</template>

<script>
import MessageInput from '@/components/MessageInput';
import MessageBody from '@/components/MessageBody';
import { mapGetters } from 'vuex';

function scrollBottom() {
  this.$refs.chatContainer
    .scrollTo(0, this.$refs.chatContainer.scrollHeight);
}
export default {
  name: 'ContainerChat',
  components: {
    MessageBody,
    MessageInput,
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters([
      'getMessages',
      'getLoggedIn',
      'getCurrent',
      'getType',
    ]),
    messages() {
      this.$nextTick(scrollBottom);

      return this.$store.getters[`${this.getType}/getMessages`](this.getCurrent);
    },
    emptymsg() {
      return this.messages
        ? Object.keys(this.$store.getters[`${this.getType}/getMessages`](this.getCurrent)).length
        : false;
    },
    isLoggedIn() {
      return this.getLoggedIn;
    },
    isActiveChat() {
      return this.getCurrent;
    },
  },
};
</script>

<style scoped>
.chat-container {
  display: block;
  height: inherit;
  padding: 2% 2%;
  box-sizing: border-box;
  height: calc(100vh - 7.5rem);
  overflow-y: auto;
}

h4 {
  color: #6aacde;
}
</style>
