      </div>
    </div>
    <hr>
    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Address</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          v-model="addressCo2"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="address"
          aria-label="address"
          readonly="readonly">
      </div>
    </div>

    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Private key</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          :maxlength="64"
          v-model="privatekeyCo2"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="Private key..."
          aria-label="Private-key"
          readonly="readonly">
      </div>
    </div>
    <h4 class="text-center">If something went wrong,
    you could refresh page and insert your private key</h4>
    <h4 class="text-center">(registration will be continued automatically)</h4>
    <Loader :height="220"/>
  </div>

</template>
<script>

import AccessController from '@/modules/controllers/access';
import AccountController from '@/modules/controllers/account';
import Loader from '@/components/Loader';
import { mapActions, mapGetters } from 'vuex';
import transactionTransfer from '@/modules/transfertransaction';
import RecursionExecute from '@/modules/utils/recursion.execute';
import Qrcode from '@/components/Qr';

const user = AccessController.create();
const accountInf = new AccountController();
const transfer = transactionTransfer.create();
const recexec = new RecursionExecute();
export default {
  name: 'Signup',
  components: {
    Loader,
    Qrcode,
  },

  data() {
    return {
      address: '',
      publickey: '',
      privatekey: '',
      privatekeyCo1: '',
      privatekeyCo2: '',
      addressCo1: '',
      addressCo2: '',
      statusbalance: 'waiting',
      statusregistration: 'waiting',
    };
  },

  methods: {
    ...mapActions([
      'setMe',
      'setLoggedIn',
      'setRightBar',
      'setLeftBar',
      'setNetwork',
      'setBalance',
    ]),
    ...mapGetters([
      'getNetwork',
      'getBalance',
    ]),
    async createAccount() {
      try {
        user.network = this.network;
        accountInf.network = this.network;
        await user.login(this.privatekey);
        this.address = user.data.master.address;
        this.publickey = user.data.master.publicKey;
        this.privatekey = user.data.master.privateKey;
        this.privatekeyCo1 = user.data.cosigners.Cosig1.privateKey;
        this.privatekeyCo2 = user.data.cosigners.Cosig2.privateKey;
        this.addressCo1 = user.data.cosigners.Cosig1.address;
        this.addressCo2 = user.data.cosigners.Cosig2.address;
        accountInf.address = this.address;
        const created = await this.uservalidate();
        if (!created) {
          recexec.execute = true;
          recexec.time = 10000;
          recexec.executable = this.getbalance;
          recexec.exec();
        }
      } catch (e) {
        this.$bus.$emit('alert', e);
      }
    },
    async getbalance() {
      const balance = await accountInf.getBalance(this.address);
      this.setBalance(balance);
    },
    async uservalidate() {
      const valid = await user.validateAccount(this.address);
      if (!valid) return false;
      this.setMe(user.data.master.address);
      this.setLoggedIn(true);
      this.setRightBar('ChatContainer');
      this.setLeftBar('DialogList');
      return true;
    },
    statusBalance() {
      this.statusbalance = 'complete!';
    },
    statusRegistration() {
      this.statusregistration = 'in process';
    },
  },

  computed: {
    network: {
      set(val) {
        this.setNetwork(val);
      },
      get() {
        return this.getNetwork();
      },
    },
    balance() {
      return this.getBalance();
    },
  },

  mounted() {
    this.privatekey = user.createPrivateKey();
  },

  watch: {
    network() {
      this.createAccount();
    },
    privatekey() {
      this.createAccount();
    },
    async balance() {
      if (this.balance >= 1) {
        try {
          this.statusBalance();
          // create multisig account
          await transfer.Send('aggregateTransaction');
          this.statusRegistration();
          // start checking user registration in loop
          recexec.executable = this.uservalidate;
        } catch (e) {
          this.$bus.$emit('alert', e);
        }
      }
    },
  },

  beforeDestroy() {
    // stop checking user registration in loop
    recexec.execute = false;
  },

};
</script>
