<template>
  <div class="row-fluid">
    <div class="col-md-6 offset-md-3 pt-2">
      <h4 class="modal-title">Node list</h4>
      <div class="form-group">
        <textarea
          v-model="text"
          class="form-control"
          rows="15" />
        <button
          @click = "pushNodes"
          :disabled="disabled"
          type="button"
          class="mt-2 btn btn-primary btn-custom">Update</button>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable import/extensions */
/* eslint-disable import/no-unresolved */
import { mapGetters } from 'vuex';
import nem from 'nem-sdk';
import deffer from '@/modules/utils/deffer';

class HandlerNodes {
  constructor() {
    this.$nodes = [];
    this.$list = '';
  }

  set nodes(val) {
    this.$nodes.push({ uri: val });
  }

  get nodes() {
    return this.$nodes;
  }

  set list(val) {
    this.$list += `${val.uri}\n`;
  }

  get list() {
    return this.$list;
  }

  flushList() {
    this.$list = '';
  }

  flushNodes() {
    this.$nodes = [];
  }
}

const handlerNodes = new HandlerNodes();

function loopArray(obj, func, method) {
  const deffered = deffer();
  if (typeof (obj) !== 'object') return deffered.reject('type is not object');
  const { length } = obj;
  let index = 0;
  const Func = func;
  const out = '';
  const loop = () => {
    if (index < length) {
      try {
        Func[method] = obj[index];
      } catch (e) {
        console.error(e);
      }
      index += 1;
      setTimeout(loop(), 0);
    } else {
      return deffered.resolve(out);
    }
  };
  loop();
  return deffered.promise;
}

export default {
  name: 'Nodes',
  data() {
    return {
      text: '',
      disabled: false,
    };
  },

  methods: {
    async getNodes() {
      if (this.network) {
        await loopArray(nem.model.nodes[this.network], handlerNodes, 'list');
        this.text = handlerNodes.list;
        handlerNodes.flushList();
      }
    },
    async pushNodes() {
      this.disable();
      let nodes = this.text.split('\n');
      const reg = new RegExp(`^(?:(?:https?)://|w{3}.)(?:([-A-Z0-9+&@#/%=~_|$?!:,.]*)|
                              [-A-Z0-9+&@#/%=~_|$?!:,.])*(?:([-A-Z0-9+&@#/%=~_|$?!:,.]*)|
                              [A-Z0-9+&@#/%=~_|$])`, 'i');
      nodes = await nodes.filter((n) => {
        if (reg.test(n)) return true;
        return false;
      });
      if (!nodes.length) return;
      try {
        await loopArray(nodes, handlerNodes, 'nodes');
        nem.model.nodes[this.network] = await handlerNodes.nodes;
        handlerNodes.flushNodes();
      } catch (e) {
        this.$bus.$emit('alert', e);
      }
      this.getNodes();
      this.disable();
    },
    disable() {
      this.disabled = !this.disabled;
    },
  },

  computed: {
    ...mapGetters({ network: 'getNetwork' }),
  },

  mounted() {
    this.getNodes();
  },

  watch: {
    network() {
      this.getNodes();
    },
  },
};
</script>
<style scoped>
.btn-custom {
  width: 100%;
  background-color: #6AACDE!important;
  border-color: #6AACDE;
}
.btn-custom:hover {
  border-color: #afdafb;
}
h4 {
  color: #6aacde;
}
