/* eslint-disable func-names */

import Access from './access';
import Account from './account';
import CreateFilter from '@/modules/utils/filter.transaction';
import {
  PrepareMessage,
  PreparePrivateMessage
} from '@/modules/utils/message';
import store from '@/modules/store';

const account = new Account();
const group = new Access();
let instance;
export default class DialogController {
  constructor() {
    if (instance) return instance;
    this.instance = this;
    this.store = {
      chats: {},
      lives: {},
      groups: {},
    };
  }

  setConnectors(val) {
    if (!val.type) throw new Error('nonexistent type');
    if (!val.address) throw new Error('nonexistent name');
    if (!val.connectors) throw new Error('nonexistent connectors');
    this.store[val.type][val.address] = val.connectors;
  }

  getConnectors(address, type) {
    if (!type || !this.store[type]) throw new Error('nonexistent type');
    if (!address || !this.store[type][address]) throw new Error('nonexistent name');
    return this.store[type][address];
  }

  // eslint-disable-next-line class-methods-use-this
  async addChat(address) {
    const $address = account.cleanAddress(address);
    if (store.state.chats.list[$address]) throw new Error(`${$address} is already in the Chat.`);
    account.network = store.state.base.network;
    const publickey = await account.getPublickey($address);
    if (!publickey) throw new Error('Need publick key');
    store.dispatch('chats/setChat', { address: $address, publicKey: publickey });
  }

  closeConnections(address, type) {
    if (!type || !address) throw new Error('nonexistent arguments');
    const connectors = this.getConnectors(address, type);
    if (!connectors) throw new Error('nonexistent connectors');
    connectors.map(connector => connector.close());
    delete this.store[type][address];
  }

  async addLive(address) {
    const $address = account.cleanAddress(address);
    if (store.state.lives.list[$address]) throw new Error(`${$address} is already in the Live.`);
    account.network = await store.state.base.network;

    const connectors = await account.TransactionUnconfirmed($address, 'Live');
    this.setConnectors({
      address: $address,
      type: 'lives',
      connectors,
    });
    /*
        * filtering transactions
        * argument 1 : 4100 (type of multisig transaction)
        * argument 2 : 257 (type of transfer transaction)
        * argument 3 : 1 (type of unencrypted payload)
        * argument 4 : address (recipient)
      */
    const filterLive = new CreateFilter(4100, 257, 1, $address);

    account.api.on(`transaction-unconfirmed-${$address}-Live`, async (trans) => {
      const sender = account.api.pubkeyToAddress(trans.transaction.otherTrans.signer);
      if (store.getters['lives/getIsBannedAddr'](sender)) return false;
      if (filterLive.check(trans)) {
        const text = await PrepareMessage(trans);
        store.dispatch('lives/setMessage', text);
      }
    });
    store.dispatch('lives/setLive', { address: $address });
  }
  /**
   * @desc create group from privatekey
   * @param {String} privatkey privatekey of group
   */

  async addGroup(privatkey) {
    group.network = await store.state.base.network;

    const $group = await group.createAccount(privatkey);

    $group.address = account.cleanAddress($group.address);

    if (store.state.groups.list[$group.address]) throw new Error(`${$group.address} is already in the Group.`);
    account.network = await store.state.base.network;

    const connectors = await account.TransactionUnconfirmed($group.address, 'Group');

    this.setConnectors({
      address: $group.address,
      type: 'groups',
      connectors,
    });
    /*
        * filtering transactions
        * argument 1 : 4100 (type of multisig transaction)
        * argument 2 : 257 (type of transfer transaction)
        * argument 3 : 2 (type of encrypted payload)
        * argument 4 : address (recipient)
      */
    const filterGroup = new CreateFilter(4100, 257, 2, $group.address);

    account.api.on(`transaction-unconfirmed-${$group.address}-Group`, async (trans) => {
      const sender = account.api.pubkeyToAddress(trans.transaction.otherTrans.signer);
      if (store.getters['groups/getIsBannedAddr'](sender)) return false;
      if (filterGroup.check(trans)) {
        const text = await PreparePrivateMessage(trans, $group.privateKey);
        store.dispatch('groups/setMessage', text);
      }
    });
    store.dispatch('groups/setGroup', $group);
  }
}
