import nem from 'nem-sdk';
import API from '@/modules/api';

export default class AccountController {
  constructor() {
    this.api = new API();
    this.countWebsocket = 5;
  }

  cleanAddress(address) {
    return nem.model.address.clean(address);
  }

  set network(network) {
    this.api.network = network;
  }

  async getBalance(address) {
    const r = await this.api.getAddressData(address);
    return `${nem.utils.format.nemValue(r.account.balance)[0]}.${nem.utils.format.nemValue(r.account.balance)[1]}`;
  }

  async getPublickey(address) {
    const r = await this.api.getAddressData(address);
    return r.account.publicKey;
  }

  async BalanceSocket(address, eventname) {
    const connectors = await this.api.createConnector(address);
    connectors.map((connector) => {
      connector.listener = `${address}-${eventname}`;
      this.api.subscribeAccountData(connector);
    });

    this.api.on(`account-data-${eventname}`, (r) => {
      const balance = Number(`${nem.utils.format.nemValue(r.account.balance)[0]}.${nem.utils.format.nemValue(r.account.balance)[1]}`);
      this.api.emit(`account-balance-${eventname}`, balance);
    });
  }

  async TransactionUnconfirmed(address, eventname) {
    const connectors = await this.api.createConnector(address, this.countWebsocket);
    connectors.map((connector) => {
      connector.listener = `${address}-${eventname}`;
      this.api.subscribeTransactionUnconfirmed(connector);
    });
    return connectors;
  }
}
