<template>
  <div>
    <b-input-group>
      <b-input-group-prepend>
        <b-btn
          v-if="type==='group'"
          @click="createGroup"
          size="sm"
          variant="primary"
          class="btn-custom"
          aria-label="Generate group"
        >>></b-btn>
      </b-input-group-prepend>
      <b-form-input
        v-model="address"
        size="sm"
        :placeholder="placeholder" />
      <b-input-group-append>
        <b-btn
          @click="adddialog"
          size="sm"
          variant="primary"
          class="btn-custom"
          aria-label="Follow group"
        >+</b-btn>
      </b-input-group-append>
    </b-input-group>
  </div>
</template>
<script>
// checked
import AccessController from '@/modules/controllers/access';

const access = new AccessController();
export default {
  name: 'DialogAdd',
  props: {
    type: {
      type: String,
      default: '',
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      address: '',
    };
  },
  methods: {
    adddialog() {
      this.$bus.$emit('AddDialog', {
        type: this.type,
        address: this.address,
      });
      this.$bus.$emit('conversation-collapse', this.type);
    },
    createGroup() {
      this.address = access.createPrivateKey();
    },
  },
};
</script>

<style scoped>
.btn-custom {
  width: 100%;
  background-color: #6AACDE!important;
  border-color: #6AACDE;
}
.btn-custom:hover {
    border-color: #6f9bbb;
}
input {
    font-size: 0.73rem!important;
}
</style>
