export default class CreateFilter {
  constructor(...args) {
    this.args = args;
  }

  check({ transaction }) {
    return transaction.type === this.args[0]
        && transaction.otherTrans.type === this.args[1]
        && transaction.otherTrans.message.type === this.args[2]
        && transaction.otherTrans.recipient === this.args[3];
  }
}
