<template>
  <img
    :src="string"
    :style="styleObj" >
</template>
<script>
import QRcode from 'qrcode';

export default {
  name: 'Qrcode',
  props: {
    text: {
      type: String,
      default: '',
    },
    size: {
      type: Number,
      default: 98,
    },
  },

  data() {
    return {
      string: '',
      styleObj: {
        width: `${this.size}px`,
      },
    };
  },

  watch: {
    async text() {
      this.string = await QRcode.toDataURL(this.text, { errorCorrectionLevel: 'H' });
    },
  },
};
</script>
<style scoped>

</style>
