<template>
  <div
    class="message-input ml-2"
    :disabled="!isActiveText">
    <span class="charcounter">{{ messageByte }} <span class="small">CByte</span></span>
    <textarea
      ref="messageInput"
      placeholder="message..."
      v-model="message"
      @keydown.enter="submitMessage"/>
    <button
      class="send"
      :class="{ hidden: !isActive }">
      <svg
        @click="submitMessage"
        viewBox="100">
        <path
          d="M36.511,17.594L2.371,2.932c-0.374-0.161-0.81-0.079-1.1,0.21C0.982,3.43,0.896,
          3.865,1.055,4.241l5.613,13.263L2.082,32.295c-0.115,0.372-0.004,
          0.777,0.285,1.038c0.188,0.169,0.427,0.258,0.67,0.258c0.132,0,0.266-0.026,0.392-0.08
          l33.079-14.078c0.368-0.157,0.607-0.519,0.608-0.919S36.879,17.752,36.511,17.594z M4.632,
          30.825L8.469,18.45h8.061c0.552,0,1-0.448,1-1s-0.448-1-1-1H8.395L3.866,5.751l29.706,
          12.757L4.632,30.825z"/>
      </svg>
    </button>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import transactionTransfer from '@/modules/transfertransaction';

const Str2Buffer = new TextEncoder('utf-8');
const transfer = transactionTransfer.create();
export default {
  name: 'MessageInput',
  data() {
    return {
      amount: 0,
      message: '',
      isActive: false,
      isActiveText: true,
      messageByte: 0,
    };
  },

  mounted() {
    this.focusInput();
    this.messageByte = this.maxCount;
    this.$bus.$on('focus-message-input', () => {
      this.focusInput();
    });
  },

  computed: {
    ...mapGetters({
      maxCount: 'getMaxChar',
      chatEncrypt: 'getEncrypt',
    }),
  },

  watch: {
    async message() {
      this.isActive = !!this.message.length;
      const bufferLength = Str2Buffer.encode(this.message).length;
      this.messageByte = this.maxCount - bufferLength;
      if (bufferLength > this.maxCount) {
        this.message = this.message
          .substr(0, this.message.length - ((bufferLength - this.maxCount) / 2));
      }
    },

    maxCount() {
      this.messageByte = this.maxCount;
    },

    isActiveText() {
      this.$refs.messageInput.disabled = !this.isActiveText;
      this.$refs.messageInput.focus();
    },
  },

  methods: {
    ...mapActions([
      'setMessage',
      'setMe',
    ]),

    focusInput() {
      if (this.$refs.messageInput) {
        this.$refs.messageInput.value = '';
        this.message = '';
        this.$refs.messageInput.focus();
      }
    },

    visibility() {
      this.isActive = !this.isActive;
      this.isActiveText = !this.isActiveText;
    },

    async sending() {
      const { state } = this.$store;
      const { type } = state.base;
      if (!this.message.length || !state.base.current) throw new Error('empty message!');
      const recipient = state[type].list[state.base.current];

      const message = {
        amount: this.amount,
        recipientAddress: recipient.address,
        recipientPublicKey: recipient.publicKey,
        message: this.message,
        type: this.chatEncrypt ? 2 : 1,
        isMultisig: true,
      };

      const result = await transfer.Send('transferTransaction', message);
      this.message = '';
      if (type === 'chats') {
        this.$store.dispatch(`${type}/setMessage`, {
          hash: result.transactionHash.data.slice(0, 8),
          recipient: {
            address: recipient.address,
            publicKey: recipient.publicKey,
          },
          payload: {
            timestamp: new Date().toLocaleString(),
            text: message.message,
            sender: state.base.me,
          },
        });
      }
      return result;
    },

    async submitMessage(event) {
      if (!event.shiftKey) {
        event.preventDefault();
      } else {
        return;
      }
      this.isActive = false;
      this.isActiveText = false;
      try {
        await this.sending();
      } catch (err) {
        this.$bus.$emit('alert', err);
      }
      this.isActive = true;
      this.isActiveText = true;
    },
  },

};
</script>

