
export default {
  install(Vue) {
    const bus = new Vue();
    Object.defineProperty(Vue.prototype, '$bus', { // for "this.$bus"
      get() { return bus; },
      set(evt) { // alt way to send an event (this.$bus=['event_name',arg1,arg2])
        const $evt = typeof evt === 'string' ? [evt] : evt;
        bus.$emit(...$evt);
      },
    });

    Vue.mixin({
      created() { // add option "$bus" instead bus.$on in created hook
        const { $bus } = this.$options;
        this.$busListeners = {};
        for (const name in $bus) {
          this.$busListeners[name] = $bus[name].bind(this); // rebind and remember each declared listener
          bus.$on(name, this.$busListeners[name]); // register a listener for the event
        }
      },
      beforeDestroy() { // unreg listeners
        for (const name in this.$busListeners) bus.$off(name, this.$busListeners[name]);
        this.$busListeners = null;
      },
    });
  },
};
