import sha3 from 'crypto-js/sha3';
import nem from 'nem-sdk';
import API from '@/modules/api';
import Deffered from '@/modules/utils/deffer';

let instance;
const api = new API();
function checkcosigners(obj, cosigAddr1, cosigAddr2) {
  const count = obj.meta.cosignatories
    .reduce(
      (a, b) => a + (
        b.address === cosigAddr1
        || b.address === cosigAddr2
      ), 0,
    );
  return count >= 2;
}
function SHA3(str, iter = 1) {
  const d = Deffered();
  let index = 0;
  const loop = () => {
    str = sha3(str, { outputLength: 256 }).toString();

    if (index < iter) {
      index++;
      setTimeout(loop, 0);
    } else {
      return d.resolve(str);
    }
  };

  if (typeof iter !== 'number' || iter < 0) {
    return d.reject('Incorrect iter type or value');
  }

  loop();
  return d.promise;
}

export default class AccessController {
  constructor() {
    this.data = {};
  }

  createPrivateKey() {
    return nem.utils.convert.ua2hex(nem.crypto.nacl.randomBytes(32));
  }

  createAccount(privateKey) {
    const network = this.data.network === 'testnet' ? -104 : 104;
    const keyPair = nem.crypto.keyPair.create(privateKey);
    const publicKey = keyPair.publicKey.toString();
    const address = nem.model.address.toAddress(publicKey, network);

    return {
      privateKey,
      publicKey,
      address,
    };
  }

  async createCosigners(privateKey) {
    if (!nem.utils.helpers.isPrivateKeyValid(privateKey)) {
      throw new Error('Private key length must be 64 or 66 characters !');
    }

    const privateKeyCosig1 = await SHA3(privateKey, 1);
    const privateKeyCosig2 = await SHA3(privateKey, 2);
    return {
      Cosig1: this.createAccount(privateKeyCosig1),
      Cosig2: this.createAccount(privateKeyCosig2),
    };
  }

  static create() {
    if (!instance) {
      instance = new AccessController();
    }

    return instance;
  }

  async signup() {
    const privateKey = await this.createPrivateKey();
    this.data.master = await this.createAccount(privateKey);
    this.data.cosigners = await this.createCosigners(this.data.master.privateKey);
  }

  async login(privateKey) {
    if (!nem.utils.helpers.isPrivateKeyValid(privateKey)) {
      throw new Error('Private key length must be 64 or 66 characters !');
    }
    this.data.master = await this.createAccount(privateKey);
    this.data.cosigners = await this.createCosigners(privateKey);
  }

  async validateAccount() {
    api.network = this.data.network;
    const addressData = await api.getAddressData(this.data.master.address);
    return checkcosigners(
      addressData,
      this.data.cosigners.Cosig1.address,
      this.data.cosigners.Cosig2.address,
    );
  }

  set network(net) {
    this.data.network = net;
  }

  get network() {
    return this.data.network;
  }

  get cosigners() {
    return this.data;
  }
}
