import DialogController from '@/modules/controllers/dialog';
import store from '@/modules/store';

const dialog = new DialogController();
async function AddDialog({ address, type }) {
  if (!type || !address) throw new Error('type or address not found');
  switch (type) {
    case 'chat':
      await dialog.addChat(address);
      break;
    case 'live':
      await dialog.addLive(address);
      break;
    case 'group':
      await dialog.addGroup(address);
      break;
  }
}
async function DelDialog({ address, type }) {
  switch (type) {
    case 'chats':
      store.dispatch('chats/delChat', address);
      store.dispatch('chats/addToBan', address);
      if (store.getters.getCurrent === address) await store.dispatch('setCurrent', null);
      break;
    case 'lives':
      await dialog.closeConnections(address, type);
      if (store.getters.getCurrent === address) await store.dispatch('setCurrent', null);
      store.dispatch('lives/delLive', address);
      break;
    case 'groups':
      await dialog.closeConnections(address, type);
      if (store.getters.getCurrent === address) await store.dispatch('setCurrent', null);
      store.dispatch('groups/delGroup', address);
      break;
  }
}
export { AddDialog, DelDialog };
