export default class RecursionExecute {
  constructor(fn = '', time = 0) {
    this.$time = time;
    this.$executable = fn;
    this.$execute = false;
  }

  exec() {
    if (this.$execute) {
      setTimeout(() => {
        try {
          this.$executable();
          this.exec();
        } catch (e) {
          this.$execute = false;
        }
      }, this.$time);
    }
  }

  set time(time) {
    this.$time = time;
  }

  set execute(exec) {
    this.$execute = exec;
  }

  set executable(fn) {
    this.$executable = fn;
  }
}
