import nem from 'nem-sdk';
import { GMTtoLocal } from './date';

const NETWORK = {
  '-1744830463': -104,
  '1744830465': 104,
};

async function PreparePrivateMessage({ transaction, meta }, recipientPrivate, type) {
  if (
    !transaction.otherTrans.timeStamp
    && !transaction.otherTrans.message
    && !transaction.signer
  ) throw new Error('PreparePrivateMessage: transaction isn\'t valid');
  const timestamp = await GMTtoLocal(nem.utils.format.nemDate(
    transaction.otherTrans.timeStamp,
  ));
  const hash = meta.hash.data.slice(0, 8);
  const message = transaction.otherTrans.message.type === 2
    ? await nem.crypto.helpers.decode(
      recipientPrivate, transaction.signer, transaction.otherTrans.message.payload
    )
    : transaction.otherTrans.message.payload;

  const text = nem.utils.convert.utf82rstr(nem.utils.convert.hex2a(message));
  const sender = nem.model.address
    .toAddress(transaction.otherTrans.signer, NETWORK[transaction.version]);
  // type determines recipient depending on type of conversation (chat/group)
  const address = type 
    ? nem.model.address.toAddress(
      transaction.otherTrans.signer,
      NETWORK[transaction.version]
    )
    : transaction.otherTrans.recipient;
  return {
    hash,
    recipient: {
      address,
      publicKey: transaction.otherTrans.signer,
    },
    payload: {
      timestamp,
      text,
      sender,
    },
  };
}
async function PrepareMessage({ transaction, meta }) {
  if (
    !transaction.otherTrans.timeStamp
    && !transaction.otherTrans.message
    && !transaction.otherTrans.signer
    && !transaction.otherTrans.recipient
  ) throw new Error('PrepateMessage: transaction isn\'t valid');
  const timestamp = await GMTtoLocal(
    nem.utils.format.nemDate(transaction.otherTrans.timeStamp),
  );
  const hash = meta.hash.data.slice(0, 8);
  const text = nem.utils.convert.utf82rstr(
    nem.utils.convert.hex2a(transaction.otherTrans.message.payload)
);
  const sender = nem.model.address
    .toAddress(transaction.otherTrans.signer, NETWORK[transaction.version]);

  return {
    hash,
    recipient: {
      address: transaction.otherTrans.recipient,
      publickey: transaction.otherTrans.signer,
    },
    payload: {
      timestamp,
      sender,
      text,
    },
  };
}
export { PrepareMessage, PreparePrivateMessage };
