<template>
  <li
    :class="['dialog-item', {selected: select}]"
  >
    <div class="row mr-0 p-2">
      <div
        @click="onFocus"
        class="col-11 pr-0 cursor">
        <span class="name">{{ address }}</span>
        <span
          class="msg-count"
          v-if="unreadCount > 0">{{ unreadCount }}</span>
        <span class="msg-last">{{ lastMessage }}</span>
      </div>
      <div class="col-1 text-center pr-0">
        <span
          class="close"
          aria-label="Remove dialog"
          @click="delDialog">
          <span
            aria-hidden="true"
            class="float-right">&times;</span>
        </span>
      </div>
  </div></li>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import Sound from '../modules/sound';

export default {
  name: 'DialogItem',
  props: {
    type: {
      type: String,
      default: null,
      required: true,
      validator(value) {
        return [
          'chats',
          'lives',
          'groups',
        ].indexOf(value) !== -1;
      },
    },
    address: {
      type: String,
      default: null,
      required: true,
    },
    encrypt: {
      type: Boolean,
      default: true,
      required: true,
    },
    maxchar: {
      type: Number,
      default: 0,
      required: true,
    },
  },

  data() {
    return {
      unreadCount: 0,
      Type: this.type,
      Address: this.address,
      Encrypt: this.encrypt,
      Maxchar: this.maxchar,
      ClassSelect: '',
    };
  },

  methods: {
    ...mapActions([
      'setCurrent',
      'setEncrypt',
      'setType',
      'setMaxChar',
      'setRightBar',
    ]),
    async onFocus(event) {
      this.setRightBar('ContainerChat');
      this.$bus.$emit('focus-message-input', event);
      this.$bus.$emit('mobileToggleBar');
      this.setCurrent(this.dialogAddress);
      this.setEncrypt(this.dialogEncrypt);
      this.setType(this.dialogType);
      this.setMaxChar(this.dialogMaxChar);
      this.resetUnread();
    },
    incUnread() {
      this.unreadCount += 1;
    },
    resetUnread() {
      this.unreadCount = 0;
    },
    delDialog() {
      this.$bus.$emit('DelDialog', { type: this.type, address: this.address });
    },
  },

  computed: {
    ...mapGetters({
      isActiveSound: 'getSound',
    }),
    dialogType() {
      return this.Type;
    },
    dialogAddress() {
      return this.Address;
    },
    dialogEncrypt() {
      return this.Encrypt;
    },
    dialogMaxChar() {
      return this.Maxchar;
    },
    lastMessage() {
      return this.$store.getters[`${this.type}/getLastMessage`](this.dialogAddress);
    },
    select() {
      return this.$store.state.base.current === this.dialogAddress
              && this.$store.state.base.type === this.dialogType;
    },
  },

  watch: {
    lastMessage() {
      if (this.isActiveSound) Sound('static/sound/ding.ogg');
      if (this.$store.state.base.current !== this.dialogAddress) this.incUnread();
    },
  },

  beforeDestroy() {
    this.Encrypt = true;
    this.Address = null;
    this.Type = null;
  },
};
</script>


<style scoped>
.dialog-item {
  width: 100%;
  box-sizing: border-box;
  border-bottom: solid 1px #6aacde;
}
.cursor {
  cursor: pointer;
}
.selected {
  background: #e0f2ff;
}

.dialog-item img {
  border-radius: 50%;
  height: 100%;
  margin-right: 5%;
  float: left;
}

.dialog-item span {
  display: block;
  text-align: left;
}

.text {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  float: left;
  right: 0.5rem;
}

span.name {
  text-align: end;
  font-weight: bold;
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #000;
}

span.msg-last {
  text-align: end;
  color: rgb(158, 156, 156);
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.close {
    float: none!important;
    color: #6aacde!important;
    line-height: 1.3;
}
.close:hover {
  color: #2779b8!important;
}
.msg-count {
    font-size: 0.8rem;
    font-weight: bolder;
    overflow: hidden;
    background: #299aef;
    float: left;
    color: #fff;
    padding: 0 5px;
    text-overflow: ellipsis;
    max-width: 13%;
}
</style>
