<template>
  <div
    @click="switcher"
    class="message-body"
    :class="{out: out}">
    <div
    class="message-text">
      <span class="sender">{{ sender }}
        <div
          class="btn-group"
          v-show="this.show && !this.out">
          <!-- <button
            class="btn btn-sm btn-success"
            @click="copy">Copy</button> -->
          <button
            class="btn btn-sm btn-danger"
            @click="block">{{ btnValue }}</button>
      </div></span>
      <span class="timestamp">{{ timestamp }}</span>
      <span class="text">{{ text }}</span>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import store from '@/modules/store';

export default {
  name: 'MessageBody',
  props: {
    timestamp: {
      type: String,
      default: '',
    },
    text: {
      type: String,
      default: '',
    },
    out: {
      type: Boolean,
      default: false,
    },
    sender: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      show: false,
      blocked: false,
    };
  },
  computed: {
    ...mapGetters([
      'getType',
    ]),
    btnValue() {
      return this.blocked ? 'unblock' : 'block';
    },
  },
  methods: {
    ...mapActions({
      addToBanchats: 'chats/addToBan',
      addToBanlives: 'lives/addToBan',
      addToBangroups: 'groups/addToBan',
      delFromBanchats: 'chats/delFromBan',
      delFromBanlives: 'lives/delFromBan',
      delFromBangroups: 'groups/delFromBan',
    }),
    chkIsBannedAddr() {
      return store.getters[`${this.getType}/getIsBannedAddr`](this.sender);
    },
    switcher() {
      this.$bus.$emit('message-menu', this._uid);
      this.show = !this.show;
      this.blocked = this.chkIsBannedAddr();
    },
    block() {
      const state = this.blocked ? 'delFromBan' : 'addToBan';
      store.dispatch(`${this.getType}/${state}`, this.sender);
      this.chkIsBannedAddr();
    },
  },
  mounted() {
    this.$bus.$on('message-menu', (id) => { if (this._uid !== id) this.show = false; });
  },
};
</script>

<style scoped>
.message-body {
  display: block;
  max-width: 50%;
  margin-bottom: 4px;
  float: left;
  clear: both;
  cursor: pointer;
}

.message-text {
  padding: 8px;
  margin: 4px;
  text-align: left;
  border-radius: 4px;
  color: white;
  background-color: #4e8cff;
  word-wrap: break-word;
}

.message-body.out {
  float: right;
}

.message-body.out .message-text {
  background-color: #09980e;
}

.message-body span {
  display: block;
}
.sender {
  font-size: 0.8rem;
  font-style: italic;
}
.timestamp {
  font-size: 0.5rem;
  font-style: italic;
}
@media (max-width: 575.98px) {
.sender {
  font-size: 0.4rem;
  font-style: italic;
}
}
.close {
  position: absolute;
  right: 32px;
  top: 32px;
  width: 32px;
  height: 32px;
  opacity: 0.3;
}
.close:hover {
  opacity: 1;
}
.close:before, .close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 2px;
  background-color: #333;
}
.close:before {
  transform: rotate(45deg);
}
.close:after {
  transform: rotate(-45deg);
}

</style>
