<template>
  <b-button
    @click="setSound"
    :size="'sm'"
    :variant="variant">
    Sound is {{ label }}
  </b-button>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'SoundButton',
  data() {
    return {};
  },

  methods: {
    ...mapActions(['setSound']),
  },

  computed: {
    ...mapGetters({
      status: 'getSound',
    }),
    label() {
      return this.status ? 'ON' : 'OFF';
    },
    variant() {
      return this.status ? 'primary' : 'danger';
    },
  },
};
</script>
